import React from 'react'
import classes from './PartnerViewInformation.module.scss'
import Star from '../../Assets/Icons/star.png'
import PropTypes from 'prop-types'
import { getNestedObjects, CompareTwoArray, CompareFacilityAddress, facilityDiscountCompareProps } from '../Utils/utils'

const DynamicViewInformationComponents = props => {
  const {
    data,
    facilityLabel,
    listStyles,
    listStyles2,
    firstColumStyle,
    secondColumnStyle,
    hideStar,
    ifHastTitle2,
    type,
    mergeColumnStyle
  } = props

  const facilityValue = facilityLabel ? (facilityLabel.length > 0 ? facilityLabel : 'None') : 'None'

  const Label = () => {
    if (type === 'facility') {
      let arr = []
      if (facilityValue.includes('fullertonOne')) {
        arr.push('Fullerton One')
      }
      if (facilityValue.includes('preferredMedicalPartner')) {
        arr.push('Preferred Medical Partner')
      }
      if (facilityValue.includes('specialAccommodation')) {
        arr.push('Special Accommodation')
      }
      if (facilityValue.includes('None')) {
        return 'None'
      }

      return arr.map(items => ` ${items}`)
    } else {
      let arr = []
      if (facilityValue.includes('fullertonOne')) {
        arr.push('Fullerton One')
      }
      if (facilityValue.includes('panelDoctor')) {
        arr.push('Panel Doctor')
      }
      if (facilityValue.includes('specialAccommodation')) {
        arr.push('Special Accommodation')
      }
      if (facilityValue.includes('None')) {
        return 'None'
      }

      return arr.map(items => ` ${items}`)
    }
  }

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <span id='star' className={hideStar ? classes.hideStar : classes.star}>
          <img src={Star} alt='star' />
          <label id='facilityAssigned' className={classes.facilityAssigned}>
            {Label()}
          </label>
        </span>
        <ul>
          {data.map((items, index) => (
            <li key={index} className={ifHastTitle2 ? (items.title2 ? listStyles2 : listStyles) : listStyles}>
              <div className={!items.fullRow ? (firstColumStyle) : mergeColumnStyle}>
                <label id={items.id}>{items.title}</label>
                <label id={items.id}> {items.value}</label>
              </div>
              <div className={secondColumnStyle}>
                <label>{items.title2}</label>
                <label>{items.value2}</label>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </React.Fragment>
  )
}

const DynamicViewInformation = React.memo(DynamicViewInformationComponents, (prevProps, nextProps) => {
  try {
    // value

    const prevPropsArr = prevProps.data.map(items => {
      return getNestedObjects(items, ['value', 'props', 'value'])
    })
    const nextPropsArr = nextProps.data.map(items => {
      return getNestedObjects(items, ['value', 'props', 'value'])
    })
    const prevPropsArr2 = prevProps.data.map(items => {
      return getNestedObjects(items, ['value2', 'props', 'value'])
    })
    const nextPropsArr2 = nextProps.data.map(items => {
      return getNestedObjects(items, ['value2', 'props', 'value'])
    })

    // errors

    const prevPropsError = prevProps.data.map(items => {
      return getNestedObjects(items, ['value', 'props', 'error'])
    })

    const nextPropsError = nextProps.data.map(items => {
      return getNestedObjects(items, ['value', 'props', 'error'])
    })

    const prevPropsError2 = prevProps.data.map(items => {
      return getNestedObjects(items, ['value2', 'props', 'error'])
    })
    const nextPropsError2 = nextProps.data.map(items => {
      return getNestedObjects(items, ['value2', 'props', 'error'])
    })

    if (prevProps.isEdit || nextProps.isEdit) {
      if (CompareTwoArray(prevPropsArr, nextPropsArr) && CompareTwoArray(prevPropsArr2, nextPropsArr2) &&
       CompareTwoArray(prevPropsError, nextPropsError) && CompareTwoArray(prevPropsError2, nextPropsError2)) {
        return true
      }
      return false
    } else {
      if (prevProps.facility || nextProps.facility) {
        console.log('prevProps', prevProps, 'nextProps', nextProps)
        if (CompareFacilityAddress(prevProps, nextProps, ['barangayError', 'barangayItems', 'barangayValue']) &&
        CompareFacilityAddress(prevProps, nextProps, ['cityMunError', 'citiesItems', 'cityValue']) &&
        CompareFacilityAddress(prevProps, nextProps, ['districtProvError', 'provincesItems', 'districtValue']) &&
        CompareFacilityAddress(prevProps, nextProps, ['regionError', 'regionItems', 'regionValue']) &&
        CompareFacilityAddress(prevProps, nextProps, ['streetError', 'streetRef', '']) &&
        CompareFacilityAddress(prevProps, nextProps, ['zipRef', 'zipError', '']) &&
        CompareTwoArray(prevPropsArr, nextPropsArr) && CompareTwoArray(prevPropsArr2, nextPropsArr2) &&
        CompareTwoArray(prevPropsError, nextPropsError) && CompareTwoArray(prevPropsError2, nextPropsError2) &&
        facilityDiscountCompareProps(prevProps, nextProps)
        ) {
          return true
        }
        return false
      }
    }
  } catch (e) {
    return false
  }
})

export default DynamicViewInformation

DynamicViewInformation.propTypes = {
  data: PropTypes.array.isRequired,
  facilityLabel: PropTypes.array,
  firstColumStyle: PropTypes.any,
  secondColumnStyle: PropTypes.any,
  listStyles: PropTypes.any,
  listStyles2: PropTypes.any,
  ifHastTitle2: PropTypes.bool,
  type: PropTypes.any,
  mergeColumnStyle: PropTypes.any
}
