import React from 'react'

const NoSpecialCharHook = (onChange = null, specialValue) => {
  const numRef = React.useRef('')
  const [str, setNum] = React.useState('')
  React.useEffect(() => {
    setNum(specialValue)
  }, [specialValue])

  const onChangeInput = ({target: {name, value}}) => {
    let regex = /[^a-zA-Z0-9 ]/g
    numRef.current = value
    if (!regex.test(value)) {

    }
    let str = value.replace(regex, '')
    let letterNumber = /\w+/

    let e = {
      target: {}
    }

    if (value.match(letterNumber) instanceof Array) {
      str = value.replace(regex, '')
      e.target.name = name
      e.target.value = str
      onChange(e)
      setNum(str)
    } else {
      setNum(state => state)
    }
  }

  return [str, onChangeInput]
}

export default NoSpecialCharHook
